//             </p>
//             <p>
//               <b>
//               川西线如下：
//               </b>
//               成都-雅安-泸定-康定-新都桥（休）-雅江-理塘-香格里拉镇/稻城县（休）-亚丁短线+亚丁长线（休）-稻城/香格里拉镇-新龙-甘孜-色达（休）-炉霍-道孚-八美-丹巴（休）-四姑娘山-卧龙-映秀-成都
