//               b. 稻城离色达比较远，我们冲锋陷阵也开了接近10小时，如果正常合规的车速，估计要接近12小时；
//               c. 香格里拉和稻城很近，是接入亚丁和离开亚丁休息过夜的中途地点，我们为了方便定住宿所以都住稻城，大家也可以一天住香格里拉，一天住稻城。但建议是香格里拉-亚丁-稻城，因为地理位置上是香格里拉-稻城-亚丁，所以香格里拉-亚丁短线-亚丁长线-稻城是对时间和体力安排上比较合理的安排。具体长短线和体力消耗后续会讲。
