//               四川！四川！四川！当然基本都是辣的啦。路途就不要要求有什么好吃的啦，或者说不要希望有好吃不贵的店，除了成都。具体在后面会推荐一些我吃过的。
//             </p>
//             <p>
//               <b>4. 住宿</b>
//               某猪上订的。如果非节假日的旺季，其实可以到点再找旅馆，基本都有位置，价格就不确定是否会便宜。但，如果在网上预订的，千万千万不要订太便宜的！不要太轻信图片！血的教训（哭泣）
//             </p>
//   `
//
// })
const defaultState = fromJS({
  title:'',
  content:''
})
export default (state = defaultState, action) => {
  switch(action.type) {
    case constants.CHANGE_DETAIL:
      return state.merge({
        title: action.title,
        content: action.content
      })
    default:
      return state
  }
}



